.cards__list {
    display: flex;
    justify-content: center;
    width: calc(350px * 5);
    outline: 1px solid #fa0;
}

.card {
    width: 350px;
    @extend .paper;
    position: relative;
    padding-bottom: 20px;
    transition: .2s;
    margin-right: 40px;

    &:hover {
        transform: translateY(-10px);
    }

    &__pic {
        position: absolute;
        top: -40px;
    }


    &__title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @extend .mb-3,
        .px-7;

        &__name {
            @extend .h3;

        }

        &__price {
            @extend .h5;
            line-height: 48px;
        }
    }

    &__details {
        @extend .h5,
        .mb-3,
        .px-7;
    }
}