.banner {
    .container {
        height: 700px;
        background-position: center center;
        background-size: cover;
        background-repeat: no-repeat;

        @include mobile-lg {
            height: 650px;
        }

    }



    &__card {
        position: relative;
        background-color: #fff;

        &:after {
            @include pattern(100%);
            bottom: 0;
            left: 0;
            height: 88px;
        }

        @extend .px-6;
    }

    &__card--right {
        position: absolute;
        right: $container-s-unit;
        bottom: 120px;
        @extend .py-8;

        @include notebook {
            right: $container-s-unit-2l;
        }

        @include pad {
            right: $container-s-unit-xl;
        }

        @include mobile-sm {
            left: 0;
            right: 0;
            margin-left: 0;
            margin-right: 0;

            &:after {
                height: 39px;
            }

            bottom: 80px;
        }
    }
}

// -------------------------paging
// -------------------------index
.banner--index {
    .container {
        background-image: url('../images/banner-bg.png');
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
    }

    .banner__bao {
        top: 6px;
        right: 35%;
    }

    .banner__hand {
        top: 20px;
        right: 38%;
        width: 945px;
        animation: slideout 2s 1s 1 ease-out forwards;
    }

    @media screen and (min-width: 1560px) {
        .banner__bao {
            right: 40%;
        }

        .banner__hand {
            top: 20px;
            right: 42%;
            animation: slideout2 2.5s 3s 1 ease-out forwards;
        }

        @keyframes slideout2 {
            0% {
                transform: scale(1);
            }

            20% {
                transform: scale(1.1);
            }

            25% {
                right: 42%;
                top: 20px;
                transform: scale(1.1);

            }

            100% {
                right: 90vw;
                top: 250px;
                transform: scale(1.1);
            }
        }
    }

    @keyframes slideout {
        0% {
            transform: scale(1);
        }

        20% {
            transform: scale(1.1);
        }

        25% {
            right: 37%;
            top: 20px;
            transform: scale(1.1);

        }

        100% {
            right: 85vw;
            top: 120px;
            transform: scale(1.1);
        }
    }

    .banner__card {
        width: 20%;
    }
}

// -------------------------brand
.banner--brand {
    .container {
        background-image: url('../images/brand-banner.png');
        background-size: cover;
    }
}

// ------------------------- material
.banner--material {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;


    &--flour {
        background-image: url('../../../assets/images/material/material-1.jpg');
    }
    &--ma {
        background-image: url('../../../assets/images/material/material-2.jpg');
    }
    &--azuki {
        background-image: url('../../../assets/images/material/material-3.jpg');
    }
    &--taro {
        background-image: url('../../../assets/images/material/material-4.jpg');
    }
    &--peanut {
        background-image: url('../../../assets/images/material/material-5.jpg');
    }

}

.banner__card--material {
    width: 360px;
    height: 100%;
    padding-top: 100px;
    margin-left: $container-s-unit;

    &:after {
        height: 44px;
    }

    @include notebook {
        margin-left: $container-s-unit-2l;
    }

    @include pad {
        margin-left: $container-s-unit-xl;
    }
    @include mobile-lg {
        width: 320px;
    }
    @include mobile-xs {
        width: 280px;
    }

}