.container--introduce {
    background-color: #fff;
    position: relative;
    display: flex;
    align-items: center;
    @extend .container-l,
    .bg-shoadow;
}
.banner{
    .container.container--introduce{
        @include mobile-lg {
            height: auto;
            padding-top: 40px;
            padding-bottom: 40px;
        }
    }
}


.introduce {
    display: flex;
    align-items: center;
    width: 100%;

    @include mobile-lg {
        flex-direction: column;
    }

    &__pic {
        width: 50%;
        margin-right: 60px;

        @include notebook {
            width: 45%;
        }

        @include pad {
            margin-right: 40px;
        }

        @include mobile-lg {
            width: 60%;
            margin-right: 0;
            margin-bottom: 8px;
        }

        @include mobile-md {
            width: 70%;
        }

        @include mobile-sm {
            width: 80%;
        }

        @include mobile-xs {
            width: 100%;
        }

    }

    &__pic__label {
        position: absolute;
        top: 0;
        left: 0;
        font-size: 20px;
    }


    &__pic__img {
        padding-top: 84.7%;
        background-size: cover;
        background-repeat: no-repeat;

        transition: 1s;
        cursor: pointer;

        &:hover {
            background-image: url('../images/feature-hover.png');
        }

        &--1 {
            background-image: url('../images/feature.png');
        }

        &--28 {
            padding-top: 76%;
            background-image: url('../images/product-big-28.png');
        }

        &--half1 {
            background-image: url('../images/popular-half-1.png');
        }

        &--shop1 {
            background-image: url('../images/shop-1.png');
        }
    }


}


.breadcumb {
    top: 24px;

    @include mobile-lg{
        top: 16px;
    }
}