.header {
    background: linear-gradient(180deg, #F2F2F2 0%, #FFFDFA 14%, #FFFDFA 65%, $light-gray 100%);

    .container {
        height: 100px;
    }

}



.nav {
    display: flex;
    justify-content: center;


    &__link {
        padding: 6px 12px;
        @extend .h5;
        letter-spacing: .05rem;
        color: $primary;
        display: flex;
        align-items: center;

        &__icon {
            @include size(28px, 28px);
            display: block;
            background: url('../../assets/images/azuki-line.svg');
            margin-right: 8px;
        }

        &--active {
            background: #fff;
            font-weight: 500;

            .nav__link__icon {
                background: url('../../assets/images/azuki-dark.svg');
            }
        }

        &:hover {
            background-color: $pain-gray;
        }

        &:hover>img {
            transform: scale(1.08);
        }
    }
}


.subnav {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    @include pad{
        margin-right: 100px;
        margin-left: 100px;
    }
    @include mobile-md{
        padding: 0 20px;
    }


    &__item{
        margin-bottom: 20px;
    }

    &__item ~ &__item{
        margin-left: 52px;
    }

    // @include pad{
    //     &__item ~ &__item{
    //         margin-left: 16px;
    //     } 
    // }

    @include pad{
        &__item ~ &__item{
            margin-left: 100px;
        } 
    }

    @include mobile-sm{
        &__item ~ &__item{
            margin-left: 20px;
        } 
    }

    &__link {
        padding: 6px 12px;
        @extend .h5;
        letter-spacing: .05rem;
        color: $primary;
        display: flex;
        align-items: center;

        &--active {
            background: #fff;
            font-weight: 500;

            &>img {
                transform: scale(1.08);
            }
        }

        &:hover {
            background-color: $pain-gray;
        }

        &:hover>img {
            transform: scale(1.08);
        }
    }
}